.Results {
  margin-top: 15px;
  justify-content: center;
  height: 100%;
  text-align: center;
}

.ResultsTopic {
  text-align: left;
  font-size: 2rem;
  letter-spacing: 2.5px;
  font-family: "Roboto";
  margin-left: 4rem;
}

@media (max-width: 720px) {
  .ResultsTopic {
    margin-left: 2rem;
  }
}

@media (max-width: 375px) {
  .ResultsTopic {
    display: none;
  }
}
